// 
let year = new Date();
// 
let yearNow = year.getFullYear();
// 
let yearFirst = new Date(yearNow, 0, 1);
// 
let monthNow = year.getMonth() + 1;

// 
let pass = Math.floor((year - yearFirst) / (1000 * 60 * 60 * 24) + 1);

// 
let allDay =
  (yearNow % 4 == 0 && yearNow % 100 != 0) || yearNow % 400 == 0 ? 366 : 365;

// 
function getMonthAllDay(year, month) {
  let d = new Date(year, month, 0);
  return d.getDate();
}
let monthAllDay = getMonthAllDay(yearNow, monthNow);

// 
let date = year.getDate();

// 
let weekDay = year.getDay();

// 
let hour = year.getHours();

export { yearNow, monthNow, pass, allDay, monthAllDay, date, weekDay, hour };
