<template>
  <div id="CountDown">
    <div class="flex">
      <count-down-item
        title="Year"
        :percent="percentY"
        :detail="detailY"
      ></count-down-item>
      <count-down-item
        title="Month"
        :percent="percentM"
        :detail="detailM"
      ></count-down-item>
    </div>
    <div class="flex">
      <count-down-item
        title="Week"
        :percent="percentW"
        :detail="detailW"
      ></count-down-item>
      <count-down-item
        title="Day"
        :percent="percentH"
        :detail="detailH"
      ></count-down-item>
    </div>
    <div><slot name="addItem"></slot></div>
  </div>
</template>

<script>
import CountDownItem from "components/content/countDownItem/CountDownItem.vue";

// 
import {
  yearNow,
  monthNow,
  pass,
  allDay,
  monthAllDay,
  date,
  weekDay,
  hour,
} from "assets/js/getdate.js";

export default {
  name: "Read",
  components: {
    CountDownItem,
  },
  data() {
    return {};
  },
  computed: {
    detailY() {
      return yearNow + "" + pass + "";
    },
    detailM() {
      return monthNow + "" + date + "";
    },
    detailW() {
      return "" + (weekDay == 0 ? "" : 7 - weekDay) + "";
    },
    detailH() {
      return "" + (24 - hour) + "";
    },
    percentY() {
      return Math.round((pass / allDay) * 100);
    },
    percentM() {
      return Math.round((date / monthAllDay) * 100);
    },
    percentW() {
      if (weekDay == 0) {
        return Math.round((6 / 7) * 100);
      } else {
        return Math.round((weekDay / 7) * 100);
      }
    },
    percentH() {
      return Math.round((hour / 24) * 100);
    },
  },
  methods: {},
};
</script>

<style scoped lang="less">
.flex {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
</style>