<template>
  <div id="read">
    <nav-bar outsideWord="">
      <select
        v-model="searchType"
        name="search"
        slot="leftSpan"
        id="searchEngines"
        class="choose"
      >
        <option value="baidu"></option>
        <option value="google"></option>
        <option value="bing"></option>
        <option value="360">360</option>
        <option value="sougou"></option>
      </select>
      <input
        slot="center"
        class="search"
        type="text"
        placeholder="Enter"
        v-model="inputValue"
        @keyup.enter="search"
      />
      <span slot="rightSpan"></span>
    </nav-bar>
    <task-bar>
      <span
        id="time"
        slot="tagging"
        @mouseover="showDetailM"
        @mouseout="hideDetailM"
      >
        {{ nowTime }}
        <show-detail
          :text="'' + verse"
          :display="display"
        ></show-detail>
      </span>
      <count-down></count-down>
    </task-bar>
  </div>
</template>

<script>
import NavBar from "components/common/navbar/NavBar.vue";
import TaskBar from "components/content/taskbar/TaskBar.vue";
import CountDown from "components/content/countDown/CountDown.vue";
import CountDownItem from "components/content/countDownItem/CountDownItem.vue";
import ShowDetail from "components/content/showDetail/ShowDetail.vue";
import IsRead from "components/common/isRead/IsRead.vue";

import moment from "assets/js/moment.js";
import * as jinrishici from "jinrishici";

export default {
  name: "Read",
  components: {
    NavBar,
    TaskBar,
    CountDown,
    CountDownItem,
    ShowDetail,
    IsRead,
  },
  data() {
    return {
      inputValue: "",
      searchType: "baidu",
      nowTime: "",
      display: "none",
      verse: "",
    };
  },
  created() {
    moment.locale();
    this.nowTime = moment().format("ddd, MMMM DD, YYYY HH:mm:ss");
    jinrishici.load((result) => {
      this.verse = result.data.content;
    });
  },
  mounted() {
    setInterval(() => {
      this.nowTime = moment().format("ddd, MMMM DD, YYYY HH:mm:ss");
    }, 1000);
  },
  methods: {
    search() {
      switch (this.searchType) {
        case "baidu":
          window.location.assign(
            "https://www.baidu.com/s?wd=" + this.inputValue
          );
          break;
        case "bing":
          window.location.assign(
            "https://cn.bing.com/search?q=" + this.inputValue
          );
          break;
        case "google":
          window.location.assign(
            "https://www.google.com/search?q=" + this.inputValue
          );
          break;
        case "360":
          window.location.assign("https://www.so.com/s?q=" + this.inputValue);
          break;
        case "sougou":
          window.location.assign(
            "https://www.sogou.com/web?query=" + this.inputValue
          );
          break;
        default:
          window.open("https://www.baidu.com/s?wd=" + this.inputValue);
          break;
      }
    },
    showDetailM() {
      this.display = "block";
    },
    hideDetailM() {
      this.display = "none";
    },
    // changeTheme() {
    //   let body = document.querySelector("body");
    //   if (body.className === "dark-theme") {
    //     body.className = "light-theme";
    //   } else {
    //     body.className = "dark-theme";
    //   }
    // },
  },
};
</script>

<style scoped lang="less">
#searchEngines {
  margin-top: 0.08rem;
  display: inline-block;
  width: 0.53125rem;
  height: 0.15625rem;
  font-size: 0.1375rem;
  font-weight: 600;
  border: 0;
  background-color: @main-bgc;
  vertical-align: middle;
  cursor: pointer;
}
.showDetail {
  position: absolute;
  top: 0.4rem;
  left: 1.8rem;
  color: @main-col;
  display: none;
  font-family: "PSYCHO";
  span {
    font-size: 0.1rem !important;
  }
}
#time {
  &:hover .showDetail {
    display: block;
  }
}
.choose {
  color: @color-text;
  // 
  &:focus {
    outline: none;
  }
  option {
    color: #000;
  }
}
</style>